@mixin flex-container($justify-content, $direction) {
  display: flex;
  align-items: center;
  justify-content: $justify-content;
  flex-direction: $direction; }

.button {
 margin-left: 1%;
 width: 52px;
 padding: 0; }

.btnDisabled {
  margin-left: 1%;
  width: 52px;
  padding: 0;
  background-color: gray;
  color: white; }


.div {
  display: flex;
  align-items: center;
  justify-content: start;
  flex-direction: row;
  margin: 0;
  padding: 0; }

.div:first-child {
  padding: 15px 0 0 0; }

.divText {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  margin: 0;
  padding: 7px; }

.divHome {
  display: flex;
  align-items: center;
  justify-content: start;
  flex-direction: row;
  margin: 10px;
  padding: 0;
  border: 2px solid #5181b8;
  border-radius: 10px; }

.divError {
  display: flex;
  align-items: center;
  justify-content: start;
  flex-direction: row;
  margin: 10px;
  padding: 0;
  border: 2px solid red;
  border-radius: 10px;
  height: 55px; }

